.banner{
    height: calc(100vh);
    padding: 10px;
    margin-bottom: 5px;
}

.banner-info{
    height: calc(100vh);
    display: flex;
    justify-content: center;
    align-items: center;
}

.name{
    width: 40vw;
    text-align: right;
    font-size: 70px;
    /* transform: translate(0px, -50px); */
}

.top-bot-name{
    font-weight: 300;
    color: #777;
}

.middle-name{
    font-weight: 500;
}

.middle-line{
    width: 5px;
    height: 400px;
    background: #fff;
    margin: 50px;
}

.short-description{
    width: calc(25vw - 5px);
    font-weight: 300;
    font-size: 20px;
    color: #999;
}

.contact-link{
    display: flex;
    margin-top: 70px;
}

.contact-link > .link{
    display: flex;
    align-items: center;
    width: 110px;
    cursor: pointer;
}

.link > img{
    height: 20px;
    width: 20px;
}

.link > p{
    margin-left: 10px;
    margin-right: 30px;
}

.show-more{
    justify-content: center;
    align-items: center;
    text-align: center;
    transform: translateY(-100px);
}

.show-more > p{
    color: #555;
}

.show-line-div{
    display: flex;
    justify-content: center;
    align-items: center;
}

.show-line{
    background: #555;
    height: 5px;
    width: 25px;
}

.left-line{
    transform: translate(3px, 10px) rotateZ(30deg);
}

.right-line{
    transform: translate(-3px, 10px) rotateZ(-30deg);
}

@media (max-width: 600px){
    .banner{
        height: calc(30vh - 25px);
    }

    .name{
        text-align: center;
    }

    .middle-line{
        display: none;
    }
}

@media (max-width: 1200px){
    .banner{
        height: calc(50vh - 25px);
    }
}