.section2{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.parent-section{
    height: 100vh;
    overflow-y: scroll;
    scroll-snap-type: y mandatory;
    scrollbar-color: #555 #222;
    scrollbar-width: thin;
}

.section{
    scroll-snap-align: start;
}


/* scrollbar */
.parent-section::-webkit-scrollbar-track
{
	background-color: #222;
}

.parent-section::-webkit-scrollbar
{
	width: 6px;
	background-color: #222;
}

.parent-section::-webkit-scrollbar-thumb
{
	background-color: #555;
}

@media (max-width: 1200px){
    .parent-section{
        scroll-snap-type: none;
    }

    .section{
        scroll-snap-align: none;
    }

    .section2{
        height: 50vh;
    }
}